/* Card Section CSS: */
.card-group {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    
}

/* .card-img-top {
    height: 15.625rem;
} */

.card-img {
    height: 15.625rem;
    width: 25.625rem;
    border-radius: 0;
    
} 

.card {
    height: 15.625rem;
    width: 25.625rem;
    border-radius: 0;
    

}

.card-img {
    padding-bottom: 0;
}

.hover-text {
    position: absolute;
    margin-left: 25%;
    margin-top: 25%;
    color: pink;
}

.card-img-overlay {
    opacity: .8;
    background-color: black;
    height: 15.625rem;
    width: 25.625rem;

}


.card-title {
    margin-top: 25%;
}

.card-title {
    color: #00dae6;
}

.card-group>.card {
    flex: none;
}

