}

/* .final-row {
    background-color: lightgray;
    padding-bottom: 5.5rem;
} */

.proj {
    padding-bottom: 1.5rem;
}

.hr-text {
    line-height: 2rem;
    position: relative;
    outline: 0;
    border: 0;
    color: black;
    text-align: center;
    height: 1.5em;
    opacity: .5;
    font-size: 2.5rem;
    margin-bottom: 2.5rem;
    color: #00dae6;
}
.hr-text:before {
      content: '';
      /* // use the linear-gradient for the fading effect
      // use a solid background color for a solid bar */
      background: linear-gradient(to right, transparent, #00dae6, transparent);
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 1px;
    }
.hr-text:after {
      content: attr(data-content);
      position: relative;
      display: inline-block;
      color: black;
  
      padding: 0 .5em;
      line-height: 1.5em;
      color: #818078;
      background-color: #fcfcfa;
    }
  

@media only screen 
  and (min-device-width: 319px) 
  and (max-device-width: 480px) 
  and (-webkit-min-device-pixel-ratio: 1) {

    h1 {
        font-size: 2.5rem;
        margin-bottom: 2.5rem;
    }
    .hr-text{
        font-size: 2rem;
        margin-bottom: 2rem;
    }
    
    .subHome {
        font-size: 2rem;
    }

    .nav-item {
        display: none;
    }

    .mePic {
        height: 65%;
        width: 65%;
        padding: 0;
        right:0;
        top:0;
    }

    .aboutSection {
        margin-top: 0;
        padding-top: 0;
        margin-bottom: 0;
        padding-bottom: 0;
    }

    .aboutArticle {
        margin-bottom: 2.5rem;
        margin-top: -3.5rem;
    }

    .me {
        margin-top: 1.5rem;
        height: 350px;
    }

    .meTitle {
        margin-bottom: 2.5rem;
    }
    
    .skills {
        height: 700px;
    }
    
    #projects {
        margin-top: 70rem;
    }
    
    .icon {
        margin-top: 2rem;
    }

    .card-group {
        flex: none;
        max-width: 100%;
        margin-left: 0;
        padding-left: 0;
        padding-right: 0;
        align-items: center;
    }

    .contentLeft {
        right: 0rem;
    }
    .contentRight {
        left: 0rem;
        margin-top: 2.5rem;
    }

    .star {
        width: 2.0rem;
        left: 5rem;
    }

    .starRows {
        margin-top: 2.5rem;
    }

    .skillType {
        font-size: 1.5rem;
    }

    .card-img {
        height: 275px;
        width: 375px;
        border-radius: 0;
        
    } 
    
    .card {
        height: 275px;
        width: 375px;
        border-radius: 0; 
        border: none;  
    }

    .card-img-overlay {
        height: 275px;
        width: 375px;
    }
}

